<!--编辑和查询用户信息 -->
<template>
  <div id="upload">
    <label class="el-form-item__label" style="width: 80px;">
      头像
    </label>
    <!--elementui的上传图片的upload组件-->
    <el-upload
      class="avatar-uploader"
      action=""
      :show-file-list="false"
      :before-upload="beforeupload">
      <img v-if="src" :src="src" class="avatar">
      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
    </el-upload>
    <!--elementui的form组件-->
    <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="用户名">
            <el-input v-model="form.username" name="names" style="width:360px;"></el-input>
        </el-form-item>
        <!-- <el-form-item label="手机号">
            <el-input v-model="form.phonenumber" name="names" style="width:360px;"></el-input>
        </el-form-item> -->
        <el-form-item label="邮箱">
            <el-input v-model="form.email" name="names" style="width:360px;"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="onSubmit">修改</el-button>
        </el-form-item>
    </el-form>
    <!-- <button @click="FngetUserInformation()">获取数据</button> -->
  </div>
</template>

<script>
import url from '../../common/api'
import { mapState } from 'vuex'
    export default {
        data() {
            return {
              form: {//form里面的参数
                username: '',
                user_id : 10,
                // phonenumber:'',
                email:'',
              },
              param:new FormData(),//表单要提交的参数
              src:"", //展示图片的地址
            };
        },
        methods: {
            //阻止upload的自己上传，进行再操作
            beforeupload(file) {
                console.log(file);
                //创建临时的路径来展示图片
                var windowURL = window.URL || window.webkitURL;
                this.src=windowURL.createObjectURL(file);
                this.param.append('u_icon', file);
                return false;
            },
            onSubmit(){//表单提交的事件
                var form = this.form;
                this.param.append('username', form.username);
                this.param.append('user_id', form.user_id);
                // this.param.append('phonenumber', form.phonenumber);
                this.param.append('email', form.email);
              url.UserInformation(this.param).then(res=>{
                if(res.data.code[0]='200'){
                  this.$message.success(res.data.message[0]);
                  this.FngetUserInformation()
                }
              }).catch(err=>{
                console.log(err,'-------失败发送验证码')
              })
            },
            FngetUserInformation(){//获取的数据
              url.getUserInformation({
                "user_id":this.userInfo.user_id
              }).then(result=>{ 
                let xx=result.data.data[0].fields
                let newList={}
                newList.username=xx.username
                newList.email=xx.email
                newList.user_id=result.data.data[0].pk
                this.form=newList
                // this.src=require('../../assets/img/ceImg.jpg')
                if(xx.u_icon){
                  this.src=this.static+xx.u_icon
                }else{
                  this.src=''
                }
                
              }).catch(err=>{
                console.log(err,'-------tijiaoshibai')
              })
            }
        },
        computed:{
          ... mapState(['userInfo',"static"])
        },
        mounted(){
          this.FngetUserInformation()
        }
    }
</script>
<style lang="stylus" scoped>
  >>>.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  >>>.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  >>>.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
  }
  >>>.avatar {
    width: 100px;
    height: 100px;
    display: block;
  }
</style>


